// Framework Imports
import React from "react";
// Styles
import "./styles/logo.css";
// Logo Assets
import logo from "../../images/logo/bsg-bar-white.svg";

const Logo = () => {
  return <img className="Logo" src={logo} alt="Bomb Shelter Games" />;
};

export default Logo;



